import React from 'react'
import { Field, reduxForm } from 'redux-form';
import styles from './BookLectureForm.module.scss'


const BookLectureForm = props => {
    const { handleSubmit, pristine, reset, submitting } = props;
    return (
        <form onSubmit={handleSubmit} className={styles.form_style}>
            <div>
                <label>'</label>
                <div>
                    <Field
                        name="firstName"
                        component="input"
                        type="text"
                        placeholder="'"
                    />
                </div>
            </div>
            <div>
                <label></label>
                <div>
                    <Field
                        name="lastName"
                        component="input"
                        type="text"
                        placeholder=""
                    />
                </div>
            </div>
            <div>
                <label>Email</label>
                <div>
                    <Field
                        name="email"
                        component="input"
                        type="email"
                        placeholder=" Email"
                    />
                </div>
            </div>
            <div>
                <label></label>
                <div>
                    <Field
                        name="phone"
                        component="input"
                        type="phone"
                        placeholder=" "
                    />
                </div>
            </div>
            <div>
                <label> </label>
                <div>
                    <label>
                        <Field name="age" component="input" type="radio" value="yuong" />
                        {' '}
                     
                    </label>
                    <label>
                        <Field name="age" component="input" type="radio" value="mature" />
                        {' '}
                     
                    </label>
                </div>
            </div>
            <div>
                <label> </label>
                <div>
                    <Field name="lectureType" component="select">
                        <option />
                        <option value="narcotics">  </option>
                        <option value="aids">  /</option>
                        <option value="fairytail">  1-3 </option>
                    </Field>
                </div>
            </div>
            <div>
                <label>Notes</label>
                <div>
                    <Field name="notes" component="textarea" />
                </div>
            </div>
            <div>
                <button type="submit" disabled={pristine || submitting}></button>
                <button type="button" disabled={pristine || submitting} onClick={reset}>
                     
                </button>
            </div>
        </form>
    );
};

export default reduxForm({
    form: 'bookLecture', // a unique identifier for this form
})(BookLectureForm);
